<template>
  <div class="main-div">
    <div class="h-35 d-inline-block" style="width: 100%">
      <h5 class="align-left pad-left">Employee Leave History</h5>
    </div>
    <hr />
    <b-row class="mt-1">
      <b-col cols="1" class="col-left">Employee</b-col>
      <b-col cols="2">
        <b-form-group>
          <b-input-group>
            <b-form-input
              class="col-left form-control form-control-sm"
              v-model="employee.id"
              placeholder=""
              @change="getEmployee"
              v-b-tooltip.hover
              title="Enter employee code"
            ></b-form-input>
            <template v-slot:append>
              <searchSm
                :column1Key="searchEmp.attr1.name"
                :column1label="searchEmp.attr1.label"
                :column2Key="searchEmp.attr2.name"
                :column2label="searchEmp.attr2.label"
                :column3Key="searchEmp.attr3.name"
                :column3label="searchEmp.attr3.label"
                :column4Key="searchEmp.attr4.name"
                :column4label="searchEmp.attr4.label"
                :fetchItem="fetchEmployeeT"
                @fetchD="fetchEmployeeList"
                :items="employeeList"
                :reset="defaultState"
                :totalRowsT="totalRows"
              />
            </template>
          </b-input-group>
        </b-form-group>
      </b-col>
      <b-col cols="1" class="col-left">Name</b-col>
      <b-col cols="3" class="col-left">
        {{ this.employee.empName }}
      </b-col>
      <b-col cols="4" class="col-left">
        {{ this.employee.empDepartment }}
      </b-col>
    </b-row>
    <b-row>
      <div class="table-div">
        <b-container fluid>
          <b-table
            class="table table-bordered table-sm"
            selected-variant="active"
            :items="leaveHistoryList"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            :hover="true"
            v-model="visibleRows"
          >
            <template v-slot:cell(selected)="{ rowSelected }">
              <template v-if="rowSelected">
                <span class="sr-only">Selected</span>
              </template>
              <template v-else>
                <span class="sr-only">Not selected</span>
              </template>
            </template>
            <template slot="bottom-row" class="row-background">
              <td class="col-left">
                <h4><b-badge>Total</b-badge></h4>
              </td>
              <td></td>
              <td></td>
              <td class="text-right">{{ t1.toFixed(3) | formatter1 }}</td>
            </template>
            <template slot="bottom-row">
              <td class="text-right">{{ t2.toFixed(3) | formatter1 }}</td>
            </template>
            <template slot="bottom-row">
              <td class="text-right">{{ t3.toFixed(3) | formatter1 }}</td>
            </template>
          </b-table>
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            size="sm"
            class="my-0"
          ></b-pagination>
        </b-container>
      </div>
    </b-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import moment from 'moment'
// import VueNumeric from 'vue-numeric'
import searchSm from '../components/SearchSm.vue'
export default {
  data () {
    return {
      visibleRows: [],
      currentPage: 1,
      perPage: 5,
      // totalRows: items.length,
      pageOptions: [5, 10, 15],
      sortBy: null,
      sortDesc: false,
      // sortDirection: "asc",
      filter: '',
      sortDirection: 'asc',
      searchEmp: {
        attr1: { name: 'id', label: 'Employee Code' },
        attr2: { name: 'empName', label: 'Employee Name' },
        attr3: { name: 'cprNo', label: 'Cpr No' },
        attr4: { name: 'workDesignation', label: 'Work designation' }
      },
      leaveHistoryList: [],
      leaveHistoryList1: [],
      fields: [
        {
          key: 'id',
          label: 'Transaction Id',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'dateFrom',
          label: 'Leave From Date',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'dateTo',
          label: 'Leave Date to',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'annualLeave',
          label: 'Annual Leave',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
        },

        {
          key: 'sickLeave',
          label: 'Sick Leave',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
        },
        {
          key: 'casualLeave',
          label: 'Casual Leave',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
          // formatter (value) {
          //   return moment(value).format('DD-MM-YYYY')
          // }
        }
      ]
    }
  },
  mounted () {
    // this.fetchLeaveHistoryList(1182)
  },
  components: {
    searchSm
    // VueNumeric
  },
  computed: {
    ...mapGetters([
      'message',
      'employeeList',
      'fetchStartRec',
      'employee',
      'totalRows'
    ]),
    t1 () {
      return this.visibleRows.reduce((accum, item) => {
        return accum + parseFloat(item.annualLeave)
      }, 0.0)
    },
    t2 () {
      return this.visibleRows.reduce((accum, item) => {
        return accum + parseFloat(item.sickLeave)
      }, 0.0)
    },
    t3 () {
      return this.visibleRows.reduce((accum, item) => {
        return accum + parseFloat(item.casualLeave)
      }, 0.0)
    }
  },
  filters: {
    formatter1: function (value) {
      return new Intl.NumberFormat(undefined, {
        minimumFractionDigits: 2
      }).format(value)
    }
  },
  methods: {
    ...mapActions(['fetchEmployee', 'fetchEmployeeList', 'defaultState']),
    fetchEmployeeT (x) {
      this.fetchEmployee(x)
      this.fetchLeaveHistoryList(x)
    },
    getEmployee () {
      this.fetchEmployee(this.employee.id)
      this.fetchLeaveHistoryList(this.employee.id)
    },
    fetchLeaveHistoryList (x) {
      const url =
        process.env.VUE_APP_API_URL + `/employee/leaveTransactionList/${x}`
      // console.log('url:' + url)
      fetch(url)
        .then(res => res.json())
        .then(data => {
          // console.log('Output: ', data)
          this.leaveHistoryList = data
        })
        .catch(err => console.error(err))
    },
    formatData (value, x) {
      if (!value) return ''
      if (x.includes('date')) {
        // console.log('col name includes date')
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else if (x.includes('Date')) {
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else {
        // console.log('not formated')
        return value
      }
    }
  }
}
</script>

<style>
.main-div {
  width: 95%;
  margin-left: 20px;
  margin-right: 10px;
  font-size: medium;
}
.table-div {
  width: 90%;
  margin-left: 40px;
  margin-right: 0px;
  font-size: small;
}
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}
.input-required {
  background-color: rgb(245, 242, 212);
}

#b-col {
  text-align: left;
}

.textBoxWidth {
  width: 160px;
}
.cellColor {
  background-color: #d9dcdf;
}
.calculated-value {
  background-color: #d9dcdf;
}
.sub-head {
  background-color: #d9dcdf;
}
.group1-color {
  background-color: rgb(242, 242, 242);
}
.group2-color {
  background-color: rgb(230, 230, 230);
}
p {
  margin-bottom: 0px;
}
.spacer {
  margin-top: 5px;
  margin-bottom: 5px;
}
</style>
